<template>
  <div id="wmpWrapper">
    <home></home>
  </div>
</template>

<script>
import Vue from 'vue'
import Router from 'vue-router'
import home from './components/Home'

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      el: '#wmpWrapper'
    }
  ]
})

Vue.use(Router)

export default {
  router,
  components: {
    home
  }
}
</script>



