<template>
  <div>
    <div class="sortWrapper scrollable">
      <div class="sortList"
        v-for="x in sortList"
        :key="x.key"
        :class="sort === x.key ? 'selected' : ''"
        @click="sort = x.key"
      >{{ x.name }}</div>
    </div>
    <button class="submit" @click="submit()">적용</button>
  </div>
</template>

<script>
export default {
  props: ['sortCondition'],
  data () {
    return {
      sort: this.sortCondition,
      sortList: [
        { key: 'best', 'name': '인기순' },
        { key: 'review', 'name': '평점' },
        { key: 'priceDesc', 'name': '높은가격순' },
        { key: 'priceAsc', 'name': '낮은가격순' },
        { key: 'starDesc', 'name': '성급 높은순' },
        { key: 'starAsc', 'name': '성급 낮은순' },
        { key: 'location', 'name': '근거리순' }
      ]
    }
  },
  methods: {
    submit () {
      this.$emit('close-pop')
    }
  }
}
</script>

<style scoped>
  .sortWrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 53px;
  }
  .sortList{
    height: 68px;
    width: 100%;
    background-color: #FFF;
    padding-left: 20px;
    line-height: 67px;
    transition: background-color .3s ease;
    position: relative;
    cursor: pointer;
    border-bottom: 1px solid #ddd;
  }
  .sortList:after{
    content: '';
    width: 22px;
    height: 22px;
    border: solid 1px #bbbbbb;
    border-radius: 100%;
    position: absolute;
    right: 20px;
    top: 23px;
    box-sizing: border-box;
    transition: .3s ease;
  }
  .sortList.selected{
    background-color: #e2e0db;
  }
  .sortList.selected:after{
    background-image: url(../../assets/selected.svg);
    background-repeat: no-repeat;
    background-size: cover;
    border: 0;
  }
  .submit{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #262628;
    color: #FFF;
    height: 53px;
    border: 0;
  }
</style>



