<template>
  <div>
    <div class="autoCompleteBox">
      <div class="searchIcon"></div>
      <input ref="cityInput" placeholder="머무를 장소를 입력해주세요." @keyup="keyup()">
    </div>
    <div class="autoList">
      <div v-if="isLoading" style="font-size: 20px; padding: 50px; text-align: center;">loading</div>
      <ul>
        <li
          v-for="x in suggestion"
          :key="x.label"
          @click="$emit('change-destination', x)"
        >
          <div class="roboto">{{ x.type.substr(0, 1).toUpperCase() }}</div>
          <div v-html="highlighting(getCityView(x))"></div>
          <div v-html="highlighting(x.name)"></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Api from '../../api/api'

export default {
  name: 'CitySearch',
  props: ['inputData'],
  data () {
    return {
      destination: this.inputData.destination,
      delay: 300,
      timeout: '',
      isLoading: false,
      suggestion: []
    }
  },
  methods: {
    keyup () {
      if (this.timeout) {
        window.clearTimeout(this.timeout)
      }
      const value = this.$refs.cityInput.value
      if (!value || value.length === 0) {
        return
      }
      this.suggestion = []
      this.timeout = window.setTimeout(() => {
        this.isLoading = true
        Api.autocomplete(value).then((resp) => {
          this.suggestion = resp.data.result
          this.isLoading = false
        })
      }, this.delay)
    },
    highlighting (text) {
      const reg = new RegExp(this.$refs.cityInput.value, 'gi')
      return text.replace(reg, `<span>${this.$refs.cityInput.value}</span>`)
    },
    getCityView (x) {
      const arrText = []
      if (x.city_name) {
        arrText.push(x.city_name)
      }
      if (x.region) {
        arrText.push(x.region)
      }
      if (x.country_name) {
        arrText.push(x.country_name)
      }
      return arrText.join(', ')
    }
  },
  mounted () {
    setTimeout(() => {
      this.$refs.cityInput.value = this.destination ? this.destination.label : ''
      this.$refs.cityInput.select()
    }, 160)
  }
}
</script>

<style scoped>
  .autoCompleteBox{
    position: absolute;
    left: 16px;
    right: 60px;
    top: -60px;
    height: 60px;
  }
  .autoCompleteBox input{
    height: 40px;
    margin-top: 10px;
    background-color: #ffffff;
    border: solid 1px #d4d4d4;
    width: 100%;
    padding: 0;
    padding-left: 36px;
    font-size: 14px;
  }
  .searchIcon{
    width: 18px;
    height: 20px;
    position: absolute;
    pointer-events: none;
    background-image: url(../../assets/icon.png);
    background-repeat: no-repeat;
    background-size: contain;
    filter: brightness(0.1);
    top: 20px;
    left: 10px;
  }
  ul {
    padding: 8px 0;
    background-color: #fff;
  }
  ul > li{
    padding: 12px 20px;
    cursor: pointer;
    letter-spacing: -0.4px;
    font-size: 14px;
    position: relative;
    padding-left: 65px;
  }
  ul > li > div:nth-of-type(1){
    position: absolute;
    width: 32px;
    height: 32px;
    background-color: #676767;
    left: 20px;
    top: 12px;
    border-radius: 100%;
    text-align: center;
    color: #FFF;
    line-height: 32px;
    font-size: 12px;
  }
  ul > li > div:nth-of-type(2){
    font-weight: 500;
    color: #040404;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: clip;
  }
  ul > li > div:nth-of-type(3){
    font-weight: normal;
    color: #666666;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: clip;
  }
</style>
<style>
  #wmpHotel .autoList ul > li > div > span{
    color: #49c6c8;
  }
</style>



