<template>
  <div id="wmpHotel"
    :style="{'min-height': offsetHeight + 'px', height: offsetHeight + 'px'}"
    ref="wmpHotel"
  >
    <div class="page">
      <transition name="fade" mode="out-in">
        <router-view
          :key="getParamKey"
          :inputData="inputData"
          :hotelList="hotelList"
          :hotelIndex="hotelIndex"
          @hotel-index-change="hotelIndexChange"
        />
      </transition>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Router from 'vue-router'
import Search from './Search/Wrapper'
import HotelList from './HotelList/Wrapper'
import Detail from './Detail/Wrapper'
import Book from './Book/Wrapper'
import VueCookies from 'vue-cookies'

const router = new Router({
  routes: [
    {
      path: '/book',
      name: 'book',
      component: Book
    },
    {
      path: '/:popFlag?',
      name: 'search',
      component: Search
    },
    {
      path: '/hotelList/:destination/:checkin/:checkout/:adults/:children/:room/:popFlag?',
      name: 'hotelList',
      component: HotelList
    },
    {
      path: '/detail/:destination/:checkin/:checkout/:adults/:children/:room/:hotelKey/:popFlag?',
      name: 'detail',
      component: Detail
    }
  ]
})

Vue.use(VueCookies)

export default {
  router,
  data () {
    let inputData = {}
    if (this.$cookies.get('bp_hotel_list')) {
      const inputDataList = JSON.parse(this.$cookies.get('bp_hotel_list'))
      if (Array.isArray(inputDataList)) {
        inputData = inputDataList[inputDataList.length - 1]
      }
      if (parseInt(inputData.checkin.replace(/-/g, '')) < parseInt((new Date().toISOString().split('T')[0]).replace(/-/g, ''))) {
        const today = new Date()
        const tomorrow = new Date()
        tomorrow.setDate(today.getDate() + 1)
        inputData.checkin = `${today.getFullYear()}-${this.zeros(today.getMonth() + 1)}-${this.zeros(today.getDate())}`
        inputData.checkout = `${tomorrow.getFullYear()}-${this.zeros(tomorrow.getMonth() + 1)}-${this.zeros(tomorrow.getDate())}`
      }
    } else {
      const today = new Date()
      const tomorrow = new Date()
      tomorrow.setDate(today.getDate() + 1)
      inputData = {
        destination: '',
        checkin: `${today.getFullYear()}-${this.zeros(today.getMonth() + 1)}-${this.zeros(today.getDate())}`,
        checkout: `${tomorrow.getFullYear()}-${this.zeros(tomorrow.getMonth() + 1)}-${this.zeros(tomorrow.getDate())}`,
        adults: 1,
        children: [],
        room: 1
      }
    }
    return {
      inputData,
      offsetHeight: 0,
      hotelList: {
        param: '',
        searchTime: '',
        list: []
      },
      hotelIndex: 0
    }
  },
  computed: {
    getParamKey () {
      const key = []
      Object.keys(this.$route.params).forEach((x) => {
        if (x !== 'popFlag') {
          key.push(this.$route.params[x])
        }
      })
      return key.join('/')
    }
  },
  methods: {
    zeros (n) {
      let zero = ''
      let newN = n
      newN = n.toString()
      if (newN.length < 2) {
        for (let i = 0; i < 2 - newN.length; i += 1) {
          zero += '0'
        }
      }
      return zero + newN
    },
    hotelIndexChange (num) {
      this.hotelIndex += num
    }
  },
  mounted () {
    const height = this.$refs.wmpHotel.offsetTop
    this.offsetHeight = window.innerHeight - height
  }
}
</script>

<style>
  @import url('https://fonts.googleapis.com/css?family=Roboto+Slab');
  @import url('https://fonts.googleapis.com/css?family=Nanum+Gothic');
  #wmpHotel{
    position: relative;
    color: #333;
    font-family: 'Apple SD Gothic Neo', 'Nanum Gothic', sans-serif;
    overflow: hidden;
  }
  #wmpHotel .scrollable{
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
  }
  #wmpHotel > div{
    height: 100%;
  }
  #wmpHotel .roboto{
    font-family: 'Roboto Slab', serif !important;
  }
  #wmpHotel * {
    box-sizing: border-box;
    touch-action: manipulation;
  }
  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  button {
    padding: 0;
    background: none;
    border: 0;
    cursor: pointer;
    user-select: none;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .2s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



