<template>
  <div>
    <div v-if="loading===true" style="padding: 30px; text-align: center">loading</div>
    <div v-else>
      <div class="fixedHeader">
        <div :class="infoFix ? 'fixed' : ''">
          <div class="star"><span v-if="hotelInfo.stars" v-for="x in hotelInfo.stars" :key="x"></span></div>
          <div class="name">{{ hotelInfo.name }}</div>
          <div class="etc">
            <span>
              <span>{{ format.dateFormat(inputData.checkin) }}({{ format.getDayOfWeek(inputData.checkin) }})</span>
              <span> - </span>
              <span>{{ format.dateFormat(inputData.checkout) }}({{ format.getDayOfWeek(inputData.checkout) }})</span>
              <span> {{ format.getBetweenDay(inputData.checkin, inputData.checkout) }}박</span>
            </span>
            <span>/ 성인 {{ inputData.adults }}명, </span>
            <span v-if="inputData.children.length > 0">어린이 {{inputData.children.length}}명, </span>
            <span>객실 {{ inputData.room }}개</span>
          </div>
        </div>
        <div class="header" :class="tabFix ? 'fixed' : ''">
          <div :class="tabNum === 0 ? 'selected' : ''" @click="tabNum = 0">가격비교</div>
          <div :class="tabNum === 1 ? 'selected' : ''" @click="tabNum = 1">정보</div>
          <div :class="tabNum === 2 ? 'selected' : ''" @click="tabNum = 2">후기</div>
          <div :class="tabNum === 3 ? 'selected' : ''" @click="tabNum = 3">위치</div>
        </div>
      </div>
      <button></button>
      <div class="scrollable" ref="scrollPart">
        <div class="bgDetail"
          :style="{'background-image': `url(${Array.isArray(hotelInfo.imageUrls) ? hotelInfo.imageUrls[0] : hotelInfo.imageUrls})`}"
        >
          <div>
            <div class="star" v-if="hotelInfo.stars"><span v-for="x in hotelInfo.stars" :key="x"></span></div>
            <div class="name">{{ hotelInfo.name }}</div>
            <div class="etc">
              <span>
                <span>{{ format.dateFormat(inputData.checkin) }}({{ format.getDayOfWeek(inputData.checkin) }})</span>
                <span> - </span>
                <span>{{ format.dateFormat(inputData.checkout) }}({{ format.getDayOfWeek(inputData.checkout) }})</span>
                <span> {{ format.getBetweenDay(inputData.checkin, inputData.checkout) }}박</span>
              </span>
              <span>/ 성인 {{ inputData.adults }}명, </span>
              <span v-if="inputData.children.length > 0">어린이 {{inputData.children.length}}명, </span>
              <span>객실 {{ inputData.room }}개</span>
            </div>
          </div>
          <button class="imgMore"
            v-if="Array.isArray(hotelInfo.imageUrls) && hotelInfo.imageUrls.length > 1"
            @click="openPop('image')"
          >1/{{ hotelInfo.imageUrls.length }}</button>
        </div>
        <div class="contentDetail">
          <div class="header">
            <div :class="tabNum === 0 ? 'selected' : ''" @click="tabNum = 0">가격비교</div>
            <div :class="tabNum === 1 ? 'selected' : ''" @click="tabNum = 1">정보</div>
            <div :class="tabNum === 2 ? 'selected' : ''" @click="tabNum = 2">후기</div>
            <div :class="tabNum === 3 ? 'selected' : ''" @click="tabNum = 3">위치</div>
          </div>
          <div :style="getMinHeight">
            <div v-show="tabNum === 0">
              <room-list
                :roomInfo="hotelInfo.rooms"
                :period="format.getBetweenDay(inputData.checkin, inputData.checkout)"
              ></room-list>
            </div>
            <div v-show="tabNum === 1">
              <hotel-info
                :hotelInfo="hotelInfo"
                :isTabActive="tabNum === 1"
              ></hotel-info>
            </div>
            <div v-show="tabNum === 2">
              <review
                :hotelInfo="hotelInfo"
              ></review>
            </div>
            <div v-show="tabNum === 3">
              <position
                :hotelInfo="hotelInfo"
              ></position>
            </div>
          </div>
        </div>
      </div>
    </div>
    <image-list-popup
      v-if="$route.params.popFlag === 'image'"
      :popping="popping"
      :hotelInfo="hotelInfo"
      :inputData="inputData"
      @close-pop="closePop"
    ></image-list-popup>
  </div>
</template>

<script>
import Format from '../../util/format'
import RoomList from './RoomList'
import Review from './Review'
import HotelInfo from './HotelInfo'
import Position from './Location'
import ImageListPopup from '../HotelList/ImageListPopup'
export default {
  name: 'DetailWrapper',
  components: {
    RoomList,
    Review,
    HotelInfo,
    Position,
    ImageListPopup
  },
  props: ['hotelList'],
  data () {
    let children = []
    if (this.$route.params.children !== 'null') {
      children = this.$route.params.children.split('&')
    }
    return {
      format: Format,
      hotelInfo: {},
      loading: true,
      inputData: {
        destination: this.$route.params.destination,
        checkin: this.$route.params.checkin,
        checkout: this.$route.params.checkout,
        adults: this.$route.params.adults,
        children,
        room: this.$route.params.room
      },
      textOfMonth: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'],
      dayOfWeek: ['일', '월', '화', '수', '목', '금', '토'],
      tabNum: 0,
      infoFix: false,
      tabFix: false,
      minHeight: 0,
      popping: false
    }
  },
  computed: {
    getMinHeight () {
      return {
        'min-height': `${this.minHeight}px`
      }
    }
  },
  methods: {
    getHotelInfo () {
      // url 직접 접근이거나 해당 페이지에서 reload시 호텔로 재검색
      this.hotelInfo = {}
      this.loading = false
    },
    scrollEvent () {
      if (this.$refs.scrollPart.scrollTop > 50) {
        this.infoFix = true
        if (this.$refs.scrollPart.scrollTop > window.innerWidth - 90) {
          this.tabFix = true
        } else {
          this.tabFix = false
        }
      } else {
        this.infoFix = false
        this.tabFix = false
      }
    },
    openPop (flag) {
      const objParam = this.$route.params
      objParam.popFlag = flag
      this.$router.push({
        params: objParam
      })
      this.popping = 'open'
      setTimeout(() => {
        this.popping = false
      }, 150)
    },
    closePop () {
      this.popping = 'close'
      setTimeout(() => {
        window.history.back()
      }, 150)
    }
  },
  mounted () {
    let alreadyHotel = false
    if (Array.isArray(this.hotelList.list) && this.hotelList.list.length > 0) {
      for (let x = 0; x < this.hotelList.list.length; x += 1) {
        if (parseInt(this.hotelList.list[x].hotelId, 10) === parseInt(this.$route.params.hotelKey, 10)) {
          this.hotelInfo = this.hotelList.list[x]
          this.loading = false
          alreadyHotel = true
        }
      }
    }
    if (!alreadyHotel) {
      this.getHotelInfo()
    }
    // this.$refs.scrollPart.addEventListener('scroll', this.scrollEvent)
    const vmThis = this
    function findScrollPart () {
      if (vmThis.$refs.scrollPart) {
        vmThis.$refs.scrollPart.addEventListener('scroll', vmThis.scrollEvent)
        vmThis.minHeight = vmThis.$refs.scrollPart.clientHeight - 90 - 48
      } else {
        setTimeout(findScrollPart, 100)
      }
    }
    findScrollPart()
  }
}
</script>

<style scoped>
  .bgDetail{
    width: 100%;
    padding-bottom: 100%;
    background-size: cover;
    background-position: center center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }
  .bgDetail:before{
    height: 150px;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0), #000000);
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 0;
  }
  .bgDetail > div{
    color: #FFF;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    padding: 30px 20px 0
  }
  .bgDetail > div > .star{
    height: 15px;
    margin-bottom: 14px;
  }
  .bgDetail > div > .star > span{
    margin-right: 6px;
    float: left;
    width: 15px;
    height: 15px;
    background: url(../../assets/star.svg) no-repeat;
    background-size: cover;
  }
  .bgDetail > div > .star > span:last-of-type{
    margin-right: 0;
  }
  .bgDetail > div > .name {
    font-size: 26px;
    font-weight: 600;
    white-space: nowrap;
    text-overflow: clip;
    overflow: hidden;
  }
  .bgDetail > div > .etc {
    font-size: 12px;
    font-weight: 400;
    letter-spacing: -0.3px;
    margin-top: 4px;
  }
  button.imgMore{
    position: absolute;
    right: 10px;
    bottom: 10px;
    line-height: 22px;
    height: 22px;
    padding: 0;
    border: 0;
    background-color: transparent;
    font-size: 12px;
    color: #FFF;
    z-index: 1;
  }
  button.imgMore:after{
    content: '＋';
    display: block;
    width: 22px;
    height: 22px;
    background-color: rgba(26, 26, 28, .8);
    font-size: 20px;
    float: right;
    margin-left: 4px;
  }
  .scrollable{
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
  }
  .contentDetail{
    margin-top: 100%;
    background-color: #FFF;
    position: relative;
  }
  .header{
    height: 48px;
  }
  .header > div{
    height: 48px;
    float: left;
    width: 25%;
    box-shadow: inset 0 -1px 0 0 #ebebeb;
    text-align: center;
    line-height: 48px;
    font-size: 16px;
    font-weight: 400;
    color: #767677;
    cursor: pointer;
  }
  .header > div.selected{
    color: #111;
    border-bottom: 2px solid #1a1a1c;
  }
  .fixedHeader{
    height: auto;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  .fixedHeader > div{
    opacity: 0;
    background-color: #FFF;
    position: relative;
    pointer-events: none;
    z-index: 1;
  }
  .fixedHeader > div:first-of-type{
    transition: opacity .3s ease;
    height: 90px;
    padding: 15px 20px;
  }
  .fixedHeader > div:first-of-type.fixed{
    opacity: 1;
    pointer-events: unset;
  }
  .fixedHeader > div:last-of-type.fixed{
    opacity: 1;
    pointer-events: unset;
  }
  .fixedHeader > div:first-of-type > .star{
    height: 15px;
    margin-bottom: 7px;
  }
  .fixedHeader > div:first-of-type > .star > span{
    margin-right: 5px;
    float: left;
    width: 12px;
    height: 12px;
    background: url(../../assets/star.svg) no-repeat;
    background-size: cover;
  }
  .fixedHeader > div:first-of-type > .star > span:last-of-type{
    margin-right: 0;
  }
  .fixedHeader > div:first-of-type > .name {
    font-size: 18px;
    font-weight: 600;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: clip;
  }
  .fixedHeader > div:first-of-type > .etc {
    font-size: 12px;
    font-weight: 400;
    letter-spacing: -0.3px;
    margin-top: 4px;
  }
</style>



