<template>
  <div class="hotelPop" :class="$route.params.popFlag === 'search' ? 'search' : ''" @click="closePop">
    <div :class="popping ? popping : ''" @click.stop>
      <div class="popHeader">
        <span>{{ popTitle($route.params.popFlag) }}</span>
        <button class="close" @click="$emit('close-pop')"></button>
      </div>
      <div class="popContent">
        <hotel-filter
          v-if="$route.params.popFlag === 'filter'"
          :filterInfo="filterInfo"
          @close-pop="closePop"
        ></hotel-filter>
        <hotel-sort
          v-if="$route.params.popFlag === 'sort'"
          :sortCondition="sortCondition"
          @close-pop="closePop"
        ></hotel-sort>
        <hotel-map
          v-if="$route.params.popFlag === 'map'"
        ></hotel-map>
        <search-container
          v-if="$route.params.popFlag === 'search'"
          :inputData="inputData"
          :subClass="'sub'"
          @open-pop="openPop"
          @close-pop="closePop"
          @close-search-pop="closePop"
        ></search-container>
      </div>
    </div>
  </div>
</template>

<script>
import HotelFilter from './HotelFilter'
import HotelMap from './HotelMap'
import HotelSort from './HotelSort'
import SearchContainer from '../Search/SearchContainer'

export default {
  components: {
    HotelFilter,
    HotelSort,
    HotelMap,
    SearchContainer
  },
  name: 'SearchPopup',
  props: ['inputData', 'popping', 'filterInfo', 'sortCondition'],
  methods: {
    changeDestination (destination) {
      this.inputData.destination = {
        label: destination.label,
        lat: destination.latitude,
        lon: destination.longitude,
        type: destination.type,
        country: destination.country
      }
      this.$emit('close-pop')
    },
    changeDate (deptDate, retnDate) {
      this.inputData.checkin = deptDate
      this.inputData.checkout = retnDate
      this.$emit('close-pop')
    },
    changeOption (adults, children, room) {
      this.inputData.adults = adults
      this.inputData.children = children
      this.inputData.room = room
      this.$emit('close-pop')
    },
    popTitle (flag) {
      let title
      if (flag === 'filter') {
        title = '상세조건'
      } else if (flag === 'sort') {
        title = '정렬'
      } else if (flag === 'map') {
        title = '지도보기'
      }
      return title
    },
    openPop (flag) {
      this.$emit('open-pop', flag)
    },
    closePop () {
      this.$emit('close-pop')
    }
  }
}
</script>

<style scoped>
  .hotelPop{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(38, 38, 40, .7);
    z-index: 20;
  }
  .hotelPop > div{
    width: 100%;
    height: 100%;
    background-color: #FFF;
    position: absolute;
  }
  .hotelPop.search > div{
    background-color: transparent;
    width: 100%;
    height: 402px;
  }
  .hotelPop:not(.search) > div.open{
    animation: openPopTop linear .15s;
  }
  .hotelPop:not(.search) > div.close{
    animation: closePopTop linear .15s;
  }
  .hotelPop > div .popHeader{
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #FFF;
    line-height: 60px;
    padding-left: 20px;
    border-bottom: 1px solid #ddd;
    color: #111;
    font-size: 16px;
  }
  .hotelPop.search > div .popHeader{
    display: none;
  }
  .hotelPop > div .popHeader > button.close{
    position: absolute;
    background: transparent;
    right: 0;
    top: 0;
    height: 60px;
    width: 60px;
    border: 0;
    padding: 0;
    font-size: 32px;
    background-image: url(../../assets/close.png);
    background-repeat: no-repeat;
    background-position: center center;
  }
  .hotelPop > div .popContent {
    position: absolute;
    top: 60px;
    left: 0;
    bottom: 0;
    right: 0;
  }
  .hotelPop.search > div .popContent{
    top: 0;
  }
  @keyframes openPopTop {
    from {
      margin-top: 100vh;
    }
    to {
      margin-top: 0;
    }
  }
  @keyframes closePopTop {
    from {
      margin-top: 0;
    }
    to {
      margin-top: 100vh;
    }
  }
</style>



