<template>
  <div class="searchInfo">
    <div @click="$emit('toggle-search-info')">
      <div>{{ inputData.destination.label }}</div>
      <div>
        <span>{{ format.dateFormat(inputData.checkin) }}</span>({{ format.getDayOfWeek(inputData.checkin) }})
        <span> - </span>
        <span>{{ format.dateFormat(inputData.checkout) }}</span>({{ format.getDayOfWeek(inputData.checkout) }})
        <span> / {{ format.getBetweenDay(inputData.checkin, inputData.checkout) }}박</span>
        <span>{{ getTotalPassenger }}</span>
      </div>
      <button></button>
    </div>
    <div class="drop-shadow-down" :class="viewIndex === 0 ? '' : 'show'"></div>
  </div>
</template>

<script>
import Format from '../../util/format'
export default {
  props: ['inputData', 'viewIndex'],
  data () {
    return {
      format: Format
    }
  },
  computed: {
    getChildrenNumber () {
      return this.inputData.children.length
    },
    getTotalPassenger () {
      return parseInt(this.inputData.adults) + this.getChildrenNumber
    }
  }
}
</script>

<style scoped>
  .searchInfo{
    color: #FFF;
    background-color: #8F9D9D;
    width: 100%;
    z-index: 2;
    height: 70px;
    position: relative;
  }
  .searchInfo > div:first-of-type{
    background-color: #8F9D9D;
    padding: 17px 0 0;
    height: 70px;
    text-align: center;
    width: 100%;
  }
  .searchInfo > div button{
    border-radius: 37px;
    padding: 0;
    height: 50px;
    width: 50px;
    border: 0;
    position: absolute;
    top: 10px;
    right: 18px;
    background: url(../../assets/search-51.svg) no-repeat;
  }
  .searchInfo > div > div:first-of-type{
    line-height: 19px;
    height: 19px;
    font-size: 16px;
    padding: 0 68px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .searchInfo > div > div:last-of-type{
    line-height: 1.21;
    font-size: 14px;
    margin-top: 4px;
  }
  .searchInfo > div > div:last-of-type > span:last-of-type:before{
    content: '';
    background-image: url(../../assets/person.svg);
    background-repeat: no-repeat;
    width: 10px;
    height: 12px;
    display: inline-block;
    margin-right: 2px;
  }
  .drop-shadow-down {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: -33px;
    height: 33px;
    opacity: 0;
    background-image: linear-gradient(to bottom, #1a1a1c, rgba(26, 26, 28, 0.48) 45%, rgba(26, 26, 28, 0) 98%);
    z-index: 1;
    transition: opacity .5s ease;
    pointer-events: none;
  }
  .drop-shadow-down.show{
    opacity: .25;
  }
</style>



