<template>
  <div>
    <div class="filterWrapper scrollable">
      <div>
        <div class="title">숙소타입</div>
        <div class="content type">
          <button :class="type.length === 0 ? 'selected' : ''" @click="type = []">전체</button>
          <button
            v-for="x in typeList"
            :key="x.key"
            :class="type.indexOf(x.key) >= 0 ? 'selected' : ''"
            @click="clickType(x.key)"
          >{{ x.name }}</button>
        </div>
      </div>
      <div>
        <div class="title">호텔성급</div>
        <div class="content star">
          <button>전체</button>
          <div>
            <div v-for="x in 5" :key="x">
              <button
                :class="star.indexOf(x) >= 0 ? 'selected' : ''"
                @click="clickStar(x)"
              >{{ x }}성급</button>
            </div>
          </div>
        </div>
      </div>
      <div>
        <div class="title">총 숙박요금</div>
        <div class="content">
          <vue-slider @callback="priceSliderChange" ref="slider1" v-bind="priceSlider" v-model="priceSlider.value"></vue-slider>
          <div class="priceSection">
            <div>{{ format.numberFormat(price.min) }}원</div>
            <div>{{ format.numberFormat(price.max) }}원</div>
          </div>
        </div>
      </div>
      <div class="addition">
        <div class="title" @click="additionOpen = !additionOpen">편의시설<div class="clampIcon" v-bind:class="additionOpen ? 'selected' : ''"></div></div>
        <div class="additionOpen" :style="{height: additionOpen ? `${additionList.length * 68}px` : 0}">
          <div class="content">
            <div
              v-for="x in additionList"
              :key="x.key"
              :class="addition.indexOf(x.key) >= 0 ? 'selected' : ''"
              @click="clickAddition(x.key)"
            >{{ x.name }}</div>
          </div>
          <div class="additionButton">
            <button @click="additionAll">모두 선택</button>
            <button @click="addition = []">모두 해제</button>
          </div>
        </div>
      </div>
    </div>
    <button class="submit" @click="submit()">필터적용</button>
  </div>
</template>

<script>
import vueSlider from 'vue-slider-component'
import Format from '../../util/format'
export default {
  components: {
    vueSlider
  },
  name: 'HotelFilter',
  props: ['filterInfo'],
  data () {
    const star = []
    const type = []
    const addition = []
    for (let x = 0; x < this.filterInfo.star.length; x += 1) {
      star.push(this.filterInfo.star[x])
    }
    for (let x = 0; x < this.filterInfo.type.length; x += 1) {
      star.push(this.filterInfo.type[x])
    }
    for (let x = 0; x < this.filterInfo.addition.length; x += 1) {
      star.push(this.filterInfo.addition[x])
    }
    return {
      format: Format,
      star,
      type,
      addition,
      price: {
        min: this.filterInfo.price.min,
        max: this.filterInfo.price.max || this.filterInfo.priceRange.max
      },
      typeList: [
        { key: 'hotel', name: '호텔' },
        { key: 'resort', name: '리조트' },
        { key: 'pension', name: '펜션' },
        { key: 'motel', name: '모텔' },
        { key: 'guestHouse', name: '게스트하우스' },
        { key: 'residence', name: '레지던스' },
        { key: 'caravan', name: '카라반' },
        { key: 'glamping', name: '글램핑' }
      ],
      additionList: [
        { key: 'freeWifi', name: '무료인터넷' },
        { key: 'breakfast', name: '조식' },
        { key: 'indoorPool', name: '실내수영장' },
        { key: 'outdoorPool', name: '실외수영장' },
        { key: 'waterPool', name: '수영장' },
        { key: 'waterPark', name: '워터파크' },
        { key: 'sauna', name: '사우나' },
        { key: 'kidsCafe', name: '키즈카페' }
      ],
      priceSlider: {
        value: [
          this.filterInfo.price.min,
          this.filterInfo.price.max || this.filterInfo.priceRange.max
        ],
        width: '100%',
        height: 4,
        dotSize: 34,
        interval: 1000,
        min: this.filterInfo.priceRange.min,
        max: this.filterInfo.priceRange.max,
        disabled: false,
        show: false,
        tooltip: 'none',
        bgStyle: {
          backgroundColor: '#b9b0ae'
        },
        processStyle: {
          backgroundImage: 'linear-gradient(to right, #a9b8b8, #5c6e7c)',
          height: '4px'
        },
        sliderStyle: {
          boxShadow: '0 2px 4px 0 rgba(0, 0, 0, 0.09)',
          border: 'solid 1px #8f9d9d'
        }
      },
      additionOpen: false
    }
  },
  methods: {
    priceSliderChange (val) {
      this.price.min = val[0]
      this.price.max = val[1]
    },
    clickType (type) {
      if (this.type.indexOf(type) >= 0) {
        this.type.splice(this.type.indexOf(type), 1)
      } else {
        this.type.push(type)
      }
    },
    clickStar (star) {
      if (this.star.indexOf(star) >= 0) {
        this.star.splice(this.star.indexOf(star), 1)
      } else {
        this.star.push(star)
      }
    },
    clickAddition (addition) {
      if (this.addition.indexOf(addition) >= 0) {
        this.addition.splice(this.addition.indexOf(addition), 1)
      } else {
        this.addition.push(addition)
      }
    },
    additionAll () {
      this.addition = []
      for (let x = 0; x < this.additionList.length; x += 1) {
        this.addition.push(this.additionList[x].key)
      }
    },
    submit () {
      this.$emit('close-pop')
    }
  },
  mounted () {
    setTimeout(() => {
      this.priceSlider.show = true
    }, 160)
  }
}
</script>

<style scoped>
  .filterWrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 53px;
  }
  .filterWrapper > div{
    padding: 40px 20px 50px;
    border-bottom: 1px solid #ddd;
  }
  .filterWrapper > div:first-of-type{
    padding-bottom: 42px;
  }
  .filterWrapper > div:last-of-type{
    padding: 0;
    border-bottom: 0;
  }
  .filterWrapper div .title{
    font-size: 16px;
    color: #111;
    font-weight: 600;
    margin-bottom: 16px;
  }
  .filterWrapper > div:nth-of-type(2) .title {
    margin-bottom: 5px;
  }
  .filterWrapper > div .content.type{
    font-size: 0;
  }
  .filterWrapper > div .content.type > button{
    display: inline-block;
    margin-right: 8px;
    height: 36px;
    border-radius: 18px;
    border: solid 1px #bbbbbb;
    padding: 0 24px;
    background-color: #FFF;
    margin-bottom: 8px;
    font-size: 14px;
    color: #000;
    transition: background-color .3s ease;
  }
  .filterWrapper > div .content button.selected,
  .filterWrapper > div .content.star > div > div > button.selected{
    background-color: #8f9d9d;
    color: #FFF;
    border-color: #8f9d9d;
  }
  .filterWrapper > div .content.star > button{
    font-size: 14px;
    border: 0;
    background: transparent;
    font-weight: 300;
  }
  .filterWrapper > div .content.star > div{
    font-size: 0;
    margin-top: 16px;
  }
  .filterWrapper > div .content.star > div > div{
    display: inline-block;
    width: 20%;
    padding-right: 8px;
  }
  .filterWrapper > div .content.star > div > div:last-of-type{
    padding-right: 0;
  }
  .filterWrapper > div .content.star > div > div > button{
    width: 100%;
    height: 36px;
    border-radius: 18px;
    border: 1px solid #b9b0ae;
    padding: 0;
    background-color: #FFF;
    margin-bottom: 8px;
    font-size: 14px;
    color: #000;
    transition: background-color .3s ease;
  }
  .priceSection{
    margin-top: 16px;
    height: 22px;
    line-height: 22px;
    font-size: 16px;
    color: #000;
  }
  .priceSection > div:first-of-type{
    float: left;
  }
  .priceSection > div:last-of-type{
    float: right;
  }
  .filterWrapper div.addition .title{
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
    margin-bottom: 0;
    position: relative;
  }
  .clampIcon{
    width: 24px;
    height: 24px;
    position: absolute;
    right: 12px;
    top: 18px;
    background-image: url('../../assets/chevron-top.png');
    transform: rotate(180deg);
    transition: .5s ease;
  }
  .clampIcon.selected{
    transform: rotate(0deg);
  }
  .additionOpen{
    height: 0;
    overflow: hidden;
    transition: height .5s ease;
  }
  .filterWrapper div.addition .content > div{
    height: 68px;
    width: 100%;
    background-color: #f5f4f0;
    padding-left: 20px;
    line-height: 68px;
    transition: background-color .3s ease;
    position: relative;
  }
  .filterWrapper div.addition .content > div:after{
    content: '';
    width: 22px;
    height: 22px;
    border: solid 1px #bbbbbb;
    border-radius: 100%;
    position: absolute;
    right: 20px;
    top: 23px;
    box-sizing: border-box;
    transition: .3s ease;
  }
  .filterWrapper div.addition .content > div.selected{
    background-color: #e2e0db;
  }
  .filterWrapper div.addition .content > div.selected:after{
    background-image: url(../../assets/selected.svg);
    background-repeat: no-repeat;
    background-size: cover;
    border: 0;
  }
  .additionButton{
    font-size: 0;
    text-align: center;
    padding: 24px 0;
  }
  .additionButton > button{
    font-size: 14px;
    height: 40px;
    border-radius: 22px;
    padding: 0 40px;
  }
  .additionButton > button:first-of-type{
    background-color: #8f9d9d;
    color: #FFF;
    line-height: 40px;
    margin-right: 18px;
  }
  .additionButton > button:last-of-type{
    border: solid 1px #bbbbbb;
    line-height: 38px;
    color: #999999;
  }
  .submit{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #262628;
    color: #FFF;
    height: 53px;
    border: 0;
  }
</style>
<style>
  .filterWrapper .vue-slider-dot:after{
    content: '';
    width: 4px;
    height: 4px;
    border-radius: 2px;
    background-color: #8f9d9d;
    display: block;
    margin-top: 14px;
    margin-left: 14px;
  }
</style>



