<template>
  <div class="hotelPop" v-if="hotelInfo.imageUrls && Array.isArray(hotelInfo.imageUrls) && hotelInfo.imageUrls.length > 0">
    <div :class="popping ? popping : ''">
      <button class="imgMore" @click="$emit('close-pop')"> − </button>
      <div class="topInfo">
        <div>{{ hotelInfo.name }}</div>
        <div>
          <span>
            <span>{{ format.dateFormat(inputData.checkin) }}({{ format.getDayOfWeek(inputData.checkin) }})</span>
            <span> - </span>
            <span>{{ format.dateFormat(inputData.checkout) }}({{ format.getDayOfWeek(inputData.checkout) }})</span>
            <span> {{ format.getBetweenDay(inputData.checkin, inputData.checkout) }}박</span>
          </span>
          <span>/ 성인 {{ inputData.adults }}명, </span>
          <span v-if="inputData.children.length > 0">어린이 {{inputData.children.length}}명, </span>
          <span>객실 {{ inputData.room }}개</span>
        </div>
      </div>
      <ul class="scrollable">
        <li class="img"
          v-for="x in hotelInfo.imageUrls"
          :key="x"
          :style="{'background-image': `url(${x})`}"
        ></li>
      </ul>
    </div>
  </div>
</template>

<script>
import format from '../../util/format'

export default {
  props: ['popping', 'hotelInfo', 'inputData'],
  data () {
    return {
      format
    }
  },
  methods: {
    closePop () {
      this.$emit('close-pop')
    }
  }
}
</script>

<style scoped>
  .hotelPop{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(38, 38, 40, .7);
    z-index: 20;
  }
  .hotelPop > div{
    width: 100%;
    height: 100%;
    background-color: #FFF;
    position: absolute;
  }
  .hotelPop > div.open{
    animation: openPopTop linear .15s;
  }
  .hotelPop > div.close{
    animation: closePopTop linear .15s;
  }
  @keyframes openPopTop {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes closePopTop {
    from {
      opacity: 1;
    }
    to {
      opacity: 0;
    }
  }
  button.imgMore{
    width: 22px;
    height: 22px;
    background-color: rgba(26, 26, 28, .8);
    font-size: 20px;
    float: right;
    position: absolute;
    z-index: 3;
    right: 10px;
    top: 80px;
    color: #FFF;
  }
  .scrollable{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    padding-top: 70px;
  }
  .img{
    background-position: center center;
    background-size: cover;
    width: 100%;
    padding-bottom: 100%;
  }
  .topInfo{
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    background: rgba(255, 255, 255, .7);
    padding: 20px 20px 10px;
  }
  .topInfo > div:first-of-type{
    font-size: 20px;
    font-weight: 500;
    line-height: 17px;
  }
  .topInfo > div:last-of-type{
    font-size: 12px;
    color: #999;
    line-height: 15px;
    margin-top: 7px;
  }
</style>



