<template>
  <div>
    <ul>
      <li class="roomList" v-for="(x, index) in roomInfo" :key="index" @click="exit(x.url)">
        <div>
          <div class="logo" :class="getVendorName(x.hotelvendorKind)"></div>
          <div class="table">
            <div>{{ x.name }}</div>
            <div class="price roboto">{{ format.numberFormat(parseInt(x.price)) }}</div>
          </div>
          <div class="addition">
            <div v-for="y in getAdditionInfo(x.name)" :key="y">{{ y }}</div>
          </div>
          <div class="clampIcon"></div>
        </div>
        <div class="bottom">
          <div></div>
          <div>1박당 <span class="price2">{{ format.numberFormat(parseInt(x.price / period)) }}</span></div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Format from '../../util/format'
export default{
  props: ['roomInfo', 'period'],
  data () {
    return {
      format: Format
    }
  },
  methods: {
    getVendorName (vendor) {
      let key = 'expedia'
      if (vendor === 'BOOKING_DOT_COM') {
        key = 'booking'
      } else if (vendor === 'AGODA') {
        key = 'agoda'
      }
      return key
    },
    getAdditionInfo (name) {
      const arr = []
      if (name.indexOf('환불불가') >= 0 || name.indexOf('환불 불가') >= 0) {
        arr.push('환불불가')
      }
      if (name.indexOf('조식포함') >= 0 || name.indexOf('조식 포함') >= 0) {
        arr.push('조식포함')
      }
      if (name.indexOf('현장결제') >= 0 || name.indexOf('현장 결제') >= 0) {
        arr.push('현장결제')
      }
      return arr
    },
    exit (url) {
      window.open(url, '_blank')
    }
  }
}
</script>

<style scoped>
  .roomList{
    border-bottom: 1px solid #ddd;
    cursor: pointer;
  }
  .roomList > div:first-of-type{
    padding: 26px 20px 28px;
    position: relative;
  }
  .logo{
    height: 21px;
    background-size: contain;
    background-repeat: no-repeat;
    width: 90px;
    background-position: left center;
    margin: 2px 0;
  }
  .logo.agoda{
    background-image: url(../../assets/vendor_agoda.svg)
  }
  .logo.booking{
    background-image: url(../../assets/vendor_booking.png)
  }
  .logo.expedia{
    background-image: url(../../assets/vendor_expedia.svg)
  }
  .table{
    display: table;
    width: 100%;
    padding-top: 7px;
  }
  .table > div{
    display: table-cell;
    vertical-align: top;
  }
  .table > div:first-of-type{
    width: 60%;
    font-size: 16px;
    font-weight: 500;
  }
  .price{
    font-size: 24px;
    font-weight: normal;
    letter-spacing: -0.5px;
    color: #111;
    width: 40%;
    text-align: right;
    padding-right: 28px;
  }
  .price:before, .price2:before{
    content: '₩';
    font-family: 'sans-serif'
  }
  .bottom{
    height: 40px;
    line-height: 40px;
    background-color: #f5f4f0;
    padding: 0 20px;
    font-size: 12px;
    color: #999;
  }
  .bottom > div:first-of-type{
    float: left;
  }
  .bottom > div:last-of-type{
    float: right;
  }
  .addition {
    font-size: 0;
    margin-top: 12px;
  }
  .addition > div{
    display: inline-block;
    color: #FFF;
    height: 24px;
    line-height: 24px;
    font-size: 11px;
    font-weight: normal;
    background-color: rgba(143, 157, 157, .8);
    padding: 0 8px;
    margin-right: 4px;
  }
  .clampIcon{
    position: absolute;
    right: 20px;
    top: 62px;
    width: 24px;
    height: 24px;
    background-image: url(../../assets/chevron-top.png);
    background-repeat: no-repeat;
    transform: rotate(90deg);
  }
</style>



