<template>
  <div>
    <div class="description">
      <div class="title">호텔소개</div>
      <div
        :class="descriptionMore === 'open' ? 'open' : ''"
      ><div ref="description">{{ hotelInfo.teaser || hotelInfo.name }}</div></div>
      <div v-if="descriptionMore">
        <button
          @click="isDescriptionMoreActive = !isDescriptionMoreActive"
          :class="descriptionMore === 'open' ? 'open' : ''"
        ></button>
      </div>
    </div>
    <div class="time">
      <div>체크인 <span>{{ hotelInfo.checkin }}</span></div>
      <div>체크아웃 <span>{{ hotelInfo.checkout }}</span></div>
    </div>
    <div class="facility">
      <div
        v-for="x in facilityList"
        :key="x.key"
        :class="facility.indexOf(x.key) >= 0 ? 'active' : ''"
      ><div></div>{{ x.name }}</div>
    </div>
    <div class="addition">
      <div class="title">부대시설</div>
      <div></div>
      <div><button></button></div>
    </div>
    <div class="roomType">
      <div class="title">객실타입정보</div>
      <ul>
        <li v-for="(x, index) in roomType" :key="index">
          <div class="thumbnail"></div>
          <div>{{ x.name }}</div>
          <div>
            <span>{{ x.bed }}</span>
            <span>&nbsp;&nbsp;|&nbsp;&nbsp;{{ x.guest }}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ['hotelInfo', 'isTabActive'],
  data () {
    console.log(this.hotelInfo)
    return {
      facility: ['wifi', 'restaurant', '24front'],
      facilityList: [
        { key: 'wifi', name: '와이파이' },
        { key: 'breakfast', name: '조식' },
        { key: 'waterPool', name: '수영장' },
        { key: 'restaurant', name: '레스토랑' },
        { key: 'laundry', name: '세탁' },
        { key: 'businessCenter', name: '비즈니스센터' },
        { key: 'shuttle', name: '공항셔틀' },
        { key: 'spa', name: '스파' },
        { key: 'pets', name: '반려동물' },
        { key: 'fitness', name: '피트니스센터' },
        { key: '24front', name: '24시간프론트' },
        { key: 'parking', name: '주차' }
      ],
      additionList: [],
      roomType: [
        { name: '디럭스룸', bed: '싱글베드 2개', guest: '성인 2명' },
        { name: '디럭스룸', bed: '싱글베드 2개', guest: '성인 2명' },
        { name: '디럭스룸', bed: '싱글베드 2개', guest: '성인 2명' },
        { name: '디럭스룸', bed: '싱글베드 2개', guest: '성인 2명' },
        { name: '디럭스룸', bed: '싱글베드 2개', guest: '성인 2명' }
      ],
      isDescriptionMoreActive: false
    }
  },
  computed: {
    descriptionMore () {
      if (!this.isTabActive) {
        return false
      }
      if (this.isDescriptionMoreActive) {
        return 'open'
      }
      if (this.$refs.description && this.$refs.description.clientHeight > 100) {
        return 'hide'
      }
      return false
    }
  }
}
</script>

<style scoped>
  .title{
    font-size: 16px;
    font-weight: 600;
  }
  .description, .addition{
    padding: 40px 20px 28px;
  }
  .description > div:last-of-type, .addition > div:last-of-type{
    text-align: center;
    margin-top: 20px;
  }
  .description > div button, .addition > div button{
    font-size: 14px;
    background-color: transparent;
    border: 0;
    position: relative;
    padding-right: 16px;
    height: 20px;
  }
  .description > div button:before, .addition > div button:before{
    content: '더 보기';
  }
  .description > div button.open:before, .addition > div button.open:before{
    content: '접기';
  }
  .description > div button:after, .addition > div button:after{
    content: '';
    background-image: url(../../assets/chevron-top.png);
    width: 16px;
    height: 16px;
    position: absolute;
    top: 2px;
    right: 0;
    background-position: center center;
    background-size: contain;
    transform: rotate(180deg)
  }
  .description > div button.open:after, .addition > div button.open:after{
    transform: rotate(0deg)
  }
  .description .title, .addtion .title{
    color: #111;
  }
  .description > div:nth-of-type(2){
    font-size: 14px;
    line-height: 20px;
    margin-top: 12px;
    height: 100px;
    overflow: hidden;
    text-align: left;
  }
  .description > div:nth-of-type(2).open{
    height: auto;
  }
  .time{
    height: 50px;
    background-color: #8f9d9d;
    color: #FFF;
  }
  .time > div{
    float: left;
    width: 50%;
    position: relative;
    text-align: center;
    line-height: 50px;
    font-size: 12px;
    font-weight: 500;
  }
  .time > div:first-of-type:after{
    border-right: solid 1px rgba(255, 255, 255, .3);
    position: absolute;
    right: 0;
    top: 18px;
    height: 12px;
    content: '';
  }
  .time > div > span{
    font-size: 16px;
    font-weight: 600;
    margin-left: 12px;
  }
  .facility{
    font-size: 0;
  }
  .facility > div{
    display: inline-block;
    width: 25%;
    border: 1px solid rgba(143, 157, 157, 0.3);
    height: 100px;
    color: #bbbbbb;
    font-size: 12px;
    text-align: center;
    font-weight: 500;
  }
  .facility > div.active{
    color: #1a1a1c;
    font-weight: bold;
  }
  .facility > div > div{
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: rgba(143, 157, 157, 0.32);
    margin: 20px auto 5px;
  }
  .facility > div.active > div{
    background-color: #8f9d9d;
  }
  .facility > div:nth-of-type(1), .facility > div:nth-of-type(5), .facility > div:nth-of-type(9){
    border-left: 0;
  }
  .facility > div:nth-of-type(4), .facility > div:nth-of-type(8), .facility > div:nth-of-type(12){
    border-right: 0;
  }
  .facility > div:nth-of-type(1), .facility > div:nth-of-type(2), .facility > div:nth-of-type(3), .facility > div:nth-of-type(4){
    border-top: 0;
  }
  .roomType{
    padding: 38px 20px 65px;
    background-color: #8f9d9d;
  }
  .roomType .title{
    color: #FFF;
  }
  .roomType ul{
    overflow: hidden;
    border-radius: 4px;
    margin-top: 16px;
  }
  .roomType li{
    height: 72px;
    width: 100%;
    background-color: #FFF;
    border-bottom: 1px solid #ddd;
    padding: 16px;
    position: relative;
    padding-left: 70px;
  }
  .roomType li:last-of-type{
    border-bottom: 0;
  }
  .roomType li > div.thumbnail{
    width: 40px;
    height: 40px;
    background-color: #d5d5d5;
    border-radius: 100%;
    position: absolute;
    top: 16px;
    left: 16px;
  }
  .roomType li > div:nth-of-type(2){
    margin-top: 1px;
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    padding-right: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .roomType li > div:nth-of-type(3){
    font-size: 12px;
    font-weight: 500;
    color: #666;
    letter-spacing: -0.4px;
    margin-top: 4px;
    line-height: 15px;
  }
  .roomType li > div:nth-of-type(3) > span:last-of-type{
    color: #999;
  }
</style>



