<template>
  <div class="bpWrapper">
    <div class="scrollable">
      <search-container
        :inputData="inputData"
        :subClass="'main'"
        @open-pop="openPop"
        @close-pop="closePop"
      ></search-container>
      <div class="recentSearch" v-if="recentList.length > 0">
        <div>최근 검색한 여행</div>
        <ul>
          <li v-for="(x, index) in recentList" :key="index" @click="recentSubmit(index)">
            <div class="thumbnail roboto">{{ x.destination.type.substr(0, 1).toUpperCase() }}</div>
            <div>{{ x.destination.label }}</div>
            <div>
              <span>{{ format.dateFormat(x.checkin) }}({{ format.getDayOfWeek(x.checkin) }}) - {{ format.dateFormat(x.checkout) }}({{ format.getDayOfWeek(x.checkout) }})</span>
              <span>| 성인 {{ x.adults }}명,<span v-if="x.children.length > 0"> 어린 {{ x.children.length }}명</span> 객실 {{ x.room }}개</span></div>
            <div @click.stop @click="recentDel(index)" class="close"></div>
          </li>
        </ul>
      </div>
    </div>
    <search-popup
      v-if="$route.params.popFlag"
      :inputData="inputData"
      :popping="popping"
      :todayString="todayString"
      @close-pop="closePop"
    ></search-popup>
  </div>
</template>

<script>
import SearchContainer from './SearchContainer'
import SearchPopup from './SearchPopup'
import Format from '../../util/format'

export default {
  components: {
    SearchContainer,
    SearchPopup
  },
  props: ['inputData'],
  name: 'searchWrapper',
  data () {
    const recent = this.$cookies.get('bp_hotel_list')
    let recentList = []
    if (recent) {
      recentList = JSON.parse(recent)
    }
    return {
      popping: false,
      recentList,
      todayString: new Date().toISOString().split('T')[0],
      format: Format
    }
  },
  methods: {
    openPop (flag) {
      this.$router.push(flag)
      this.popping = 'open'
      setTimeout(() => {
        this.popping = false
      }, 150)
    },
    closePop () {
      if (!this.popping) {
        this.popping = 'close'
        setTimeout(() => {
          this.popping = false
          window.history.back()
        }, 150)
      }
    },
    recentDel (index) {
      this.recentList.splice(index, 1)
      this.$cookies.set('bp_hotel_list', JSON.stringify(this.recentList), '15d')
    },
    recentSubmit (index) {
      if (parseInt(this.recentList[index].checkin.replace(/-/g, '')) < parseInt(this.todayString.replace(/-/g, ''))) {
        alert('오늘 이전의 검색기록입니다.')
        return
      }
      const children = this.recentList[index].children.length === 0 ? 'null' : this.recentList[index].children.length
      const destination = {
        label: this.recentList[index].destination.label,
        lat: this.recentList[index].destination.lat,
        lon: this.recentList[index].destination.lon,
        type: this.recentList[index].destination.type,
        country: this.recentList[index].destination.country
      }
      const param = [
        JSON.stringify(destination),
        this.recentList[index].checkin,
        this.recentList[index].checkout,
        this.recentList[index].adults,
        children,
        this.recentList[index].room
      ]
      this.$router.push(
        `/hotelList/${param.join('/')}`
      )
    }
  }
}
</script>

<style scoped>
  .bpWrapper{
    height: 100%;
  }
  .bpWrapper .scrollable{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url('../../assets/hotel_bg.png');
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
  .recentSearch{
    padding: 0 20px;
    padding-bottom: 30px;
  }
  .recentSearch > div{
    font-size: 20px;
    text-align: center;
    color: #FFF;
    letter-spacing: -.6px;
    margin-bottom: 24px;
  }
  .recentSearch ul{
    overflow: hidden;
    border-radius: 4px;
  }
  .recentSearch li{
    height: 72px;
    width: 100%;
    background-color: #FFF;
    cursor: pointer;
    border-bottom: 1px solid #ddd;
    padding: 16px;
    position: relative;
    padding-left: 70px;
  }
  .recentSearch li:last-of-type{
    border-bottom: 0;
  }
  .recentSearch li > div.thumbnail{
    width: 40px;
    height: 40px;
    background-color: #d5d5d5;
    border-radius: 100%;
    position: absolute;
    top: 16px;
    left: 16px;
    line-height: 40px;
    text-align: center;
    font-size: 12px;
  }
  .recentSearch li > div:nth-of-type(2){
    margin-top: 4px;
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
    padding-right: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .recentSearch li > div:nth-of-type(3){
    font-size: 12px;
    font-weight: 500;
    color: #666;
    letter-spacing: -0.4px;
    margin-top: 4px;
    line-height: 15px;
  }
  .recentSearch li > div:nth-of-type(3) > span:last-of-type{
    color: #999;
  }
  .recentSearch li .close{
    background-image: url(../../assets/close-small.png);
    background-repeat: no-repeat;
    position: absolute;
    cursor: pointer;
    padding: 16px;
    background-size: 11px;
    background-position: center;
    height: 43px;
    width: 43px;
    right: 0;
    top: 0;
  }
</style>



