<template>
  <div>
    <div class="imgContainer"
      :style="{
        'background-image': getBg
      }"
    ><div></div></div>
    <div class="infoWrapper" ref="wrapper">
      <div ref="content" :style="{'margin-top': `-${scrollTop}px`}">
        <div class="hotelInfo">
          <button class="openDetail" @click="submit()">상세보기</button>
          <div>{{ hotelInfo.name }}</div>
          <div>{{ hotelInfo.address }}</div>
          <div>
            <span class="star" v-if="hotelInfo.stars" v-for="x in hotelInfo.stars" :key="x"></span>
            <span v-if="hotelInfo.rating"><span v-if="!hotelInfo.review">평점: </span>{{ hotelInfo.rating }}</span>
            <span v-if="hotelInfo.review" class="thin"> ({{ format.numberFormat(hotelInfo.review) }}개 리뷰)</span>
          </div>
          <button class="imgMore"
            v-if="Array.isArray(hotelInfo.imageUrls) && hotelInfo.imageUrls.length > 1"
            @click="$emit('open-pop', 'image')"
          >1/{{ hotelInfo.imageUrls.length }}</button>
        </div>
        <div class="agentList">
          <ul>
            <li
              v-for="(x, index) in getRoomPerAgent(hotelInfo.rooms)"
              :key="index"
              @click="exit(x.url)"
            ><button>
              <div class="roboto"><span>₩</span>{{ format.numberFormat(parseInt(x.price)) }}</div>
              <div>1박평균 <span>₩</span>{{ getPricePerDay(x.price) }}</div>
              <div class="logo" :class="getClass(x.hotelvendorKind)"></div>
            </button></li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Format from '../../util/format'
export default {
  props: ['index', 'hotelInfo', 'inputData', 'scrollTop', 'isPresent'],
  computed: {
    getBg () {
      return `url(${Array.isArray(this.hotelInfo.imageUrls) ? this.hotelInfo.imageUrls[0] : this.hotelInfo.imageUrls})`
    }
  },
  data () {
    return {
      format: Format
    }
  },
  methods: {
    getPricePerDay (price) {
      return this.format.numberFormat(parseInt(price / this.format.getBetweenDay(this.inputData.checkin, this.inputData.checkout)))
    },
    getClass (vendorName) {
      let className = 'expedia'
      if (vendorName === 'BOOKING_DOT_COM') {
        className = 'booking'
      } else if (vendorName === 'AGODA') {
        className = 'agoda'
      }
      return className
    },
    getRoomPerAgent (roomList) {
      const agentList = []
      const returnList = []
      for (let x = 0; x < roomList.length; x += 1) {
        if (agentList.indexOf(roomList[x].hotelvendorKind) < 0) {
          agentList.push(roomList[x].hotelvendorKind)
          returnList.push(roomList[x])
        }
      }
      return returnList
    },
    submit () {
      const children = this.inputData.children.length > 0 ? this.inputData.children.join('&') : 'null'
      const param = [
        JSON.stringify(this.inputData.destination),
        this.inputData.checkin,
        this.inputData.checkout,
        this.inputData.adults,
        children,
        this.inputData.room,
        this.hotelInfo.hotelId
      ]
      this.$router.push(`/detail/${param.join('/')}`)
    },
    exit (url) {
      window.open(url, '_blank')
    }
  },
  mounted () {
    if (this.isPresent) {
      this.$emit('set-client-height', this.$refs.content.clientHeight)
    }
  }
}
</script>

<style scoped>
  .imgContainer{
    width: 100%;
    padding-bottom: 80%;
    background-size: cover;
    position: relative;
    background-position: center center;
  }
  .imgContainer > div:first-of-type{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 250px;
    background-image: linear-gradient(to bottom, rgba(92, 110, 124, 0), #846b55);
  }
  .infoWrapper{
    position:absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
  }
  .hotelInfo{
    padding: 126px 20px 0;
    position: relative;
  }
  .hotelInfo > *{
    color: #FFF;
  }
  .hotelInfo button.openDetail{
    width: 63px;
    height: 25px;
    background-color: rgba(216, 216, 216, 0.3);
    border: solid 1px rgba(255, 255, 255, 0.5);
    font-size: 12px;
    padding: 0;
  }
  .hotelInfo > div:first-of-type {
    margin-top: 12px;
    font-size: 20px;
    font-weight: 600;
  }
  .hotelInfo > div:nth-of-type(2){
    font-size: 12px;
    letter-spacing: -.3px;
    margin-top: 4px;
    line-height: 17px;
    font-weight: 400;
  }
  .hotelInfo > div:nth-of-type(3){
    margin-top: 7px;
    font-size: 12px;
    letter-spacing: -.3px;
  }
  .hotelInfo > div:nth-of-type(2):before{
    width: 13px;
    height: 15px;
    float: left;
    content: '';
    margin-right: 5px;
    background: url(../../assets/location.svg) no-repeat;
  }
  .hotelInfo button.imgMore{
    position: absolute;
    right: 20px;
    bottom: 0;
    line-height: 22px;
    height: 22px;
    padding: 0;
    border: 0;
    background-color: transparent;
    font-size: 12px;
  }
  .hotelInfo > button.imgMore:after{
    content: '＋';
    display: block;
    width: 22px;
    height: 22px;
    background-color: rgba(26, 26, 28, .8);
    font-size: 20px;
    float: right;
    margin-left: 4px;
  }
  .hotelInfo .star{
    margin-right: 6px;
    float: left;
    width: 13px;
    height: 13px;
    background: url(../../assets/star.svg) no-repeat;
  }
  .hotelInfo .star:last-of-type{
    margin-right: 0;
  }
  .hotelInfo .thin{
    font-weight: 400;
  }
  .agentList{
    padding: 16px 20px 82px;
  }
  .agentList > ul > li > button{
    background-color: #EFF0F2;
    width: 100%;
    height: 80px;
    border-radius: 3px;
    margin-bottom: 10px;
    border: 0;
    padding: 0;
    position: relative;
    text-align: left;
    padding-left: 20px;
    font-weight: normal;
  }
  .agentList > ul > li:last-of-type > button{
    margin-bottom: 0;
  }
  .agentList > ul > li > button .logo{
    height: 20px;
    right: 18px;
    top: 30px;
    position: absolute;
    background-size: contain;
    background-repeat: no-repeat;
    width: 80px;
    background-position: right center;
  }
  .agentList > ul > li > button .logo.agoda{
    background-image: url(../../assets/vendor_agoda.svg)
  }
  .agentList > ul > li > button .logo.booking{
    background-image: url(../../assets/vendor_booking.png)
  }
  .agentList > ul > li > button .logo.expedia{
    background-image: url(../../assets/vendor_expedia.svg)
  }
  .agentList > ul > li > button > div:first-of-type{
    font-size: 24px;
    letter-spacing: -.5px;
    color: #111;
  }
  .agentList > ul > li > button > div:nth-of-type(2){
    font-size: 12px;
    color: #999;
  }
  .agentList > ul > li > button > div:first-of-type > span,
  .agentList > ul > li > button > div:nth-of-type(2) > span{
    font-family: sans-serif;
  }
</style>



